import  React, { useState } from "react";
/*
    board should look like this
    initialBoardState = [
        ["_","_","_"],
        ["_","_","_"],
        ["_","_","_"]
    ]
 */

const Game = () => {
    const [currentGameState, setGameState] = useState([['_','_','_'],['_','_','_'],['_','_','_']]);
    const [winner, setWinner] = useState("");
    const [active, setActive] = useState(true);
    const [firstMove, setFirstMove] = useState(true);

    function isMovesLeft(b) {
        for (let i = 0; i < 3; i++) {
            for (let j = 0; j < 3; j++) {
                if (b[i][j] === "_") {
                    return true;
                }
            }
        }
        return false;
    }

    function evaluate(b) {
        // Checking for Rows for Winner
        for (let row = 0; row < 3; row++) {
            if (b[row][0] === b[row][1] &&
                b[row][1] === b[row][2]) {
                if (b[row][0] === "X") {
                    return 10;
                }
                else if (b[row][0] === "O") {
                    return -10;
                }
            }
        }
        // Checking Columns for Winner
        for (let col = 0; col < 3; col++) {
            if (b[0][col] === b[1][col] &&
                b[1][col] === b[2][col]) {
                if (b[0][col] === "X") {
                    return 10;
                }
                else if (b[0][col] === "O") {
                    return -10;
                }
            }
        }

        // Checking Diagonals for Winner
        if (b[0][0] === b[1][1] && b[1][1] === b[2][2]) {
            if (b[0][0] === "X") { return 10; }
            else if (b[0][0] === "O") { return -10; }
        }

        if (b[0][2] === b[1][1] && b[1][1] === b[2][0]) {
            if (b[0][2] === "X") { return 10; }
            else if (b[0][2] === "O") { return -10; }
        }

        return 0;
    }

    function minimax(b, depth, isMax) {
        let score = evaluate(b);

        if (score === 10) {
            return score;
        }

        if (score === -10) {
            return score;
        }

        if (isMovesLeft(b) === false) {
            return 0;
        }

        if (isMax) {
            let best = -1000;

            for (let i = 0; i < 3; i++) {
                for (let j = 0; j < 3; j++) {
                    if (b[i][j] === "_") {
                        //Make Move
                        b[i][j] = "X";

                        best = Math.max(best, minimax(b, depth + 1, !isMax));

                        // Undo the move
                        b[i][j] = "_";
                    }
                }
            }
            return best;
        }
        else {
            let best = 1000;

            for (let i = 0; i < 3; i++) {
                for (let j = 0; j < 3; j++) {
                    if (b[i][j] === "_") {
                        //Make Move
                        b[i][j] = "O";

                        best = Math.min(best, minimax(b, depth + 1, !isMax));

                        // Undo the move
                        b[i][j] = "_";
                    }
                }
            }
            return best;
        }
    }

    function findBestMove(b) {
        let bestVal = -1000;
        let bestMove = {
            row: -1,
            col: -1
        };

        for (let i = 0; i < 3; i++) {
            for (let j = 0; j < 3; j++) {
                // Check if cell is empty
                if (b[i][j] === '_') {
                    // Make the move
                    b[i][j] = "O";

                    // compute evaluation function for this
                    // move.
                    let moveVal = minimax(b, 0, false);

                    // Undo the move
                    b[i][j] = '_';

                    // If the value of the current move is
                    // more than the best value, then update
                    // best/
                    if (moveVal > bestVal) {
                        bestMove.row = i;
                        bestMove.col = j;
                        bestVal = moveVal;
                    }
                }
            }
        }
        return bestMove;
    }

    function setSquare(row, col) {
        if (isMovesLeft(currentGameState) && currentGameState[row][col] === '_' && active) {
            let newArray = [];
            for (let i = 0; i < currentGameState.length; i++) {
                newArray[i] = currentGameState[i].slice();
            }

            newArray[row][col] = 'O';
            setGameState(newArray);

            if (evaluate(newArray) === -10) {
                setWinner("O");
                setActive(false);
                return;
            }

            let bestMove = findBestMove(newArray);
            if (bestMove.row === -1) {
                setWinner("Draw");
                return;
            }
            newArray[bestMove.row][bestMove.col] = 'X';

            setGameState(newArray);

            if (evaluate(newArray) === 10) {
                setWinner("X");
                setActive(false);
                return;
            }

            if (!isMovesLeft(newArray)) {
                setWinner("Draw");
            }
        }
    }

    if(firstMove) {
        let newArray = [];
        for (let i = 0; i < currentGameState.length; i++) {
            newArray[i] = currentGameState[i].slice();
        }

        let move = findBestMove(newArray);
        newArray[move.row][move.col] = 'X';
        setGameState(newArray);
        setFirstMove(false);
    }

    function reset() {
        setGameState([['_','_','_'],['_','_','_'],['_','_','_']]);
        setWinner("");
        setActive(true);
        setFirstMove(true);
    }

    return (
        <div>
        <div className="d-flex">
            <button onClick={() => setSquare(0,0)}> {currentGameState[0][0]} </button>
            <button onClick={() => setSquare(0,1)}> {currentGameState[0][1]} </button>
            <button onClick={() => setSquare(0,2)}> {currentGameState[0][2]} </button>
            <button onClick={() => setSquare(1,0)}> {currentGameState[1][0]} </button>
            <button onClick={() => setSquare(1,1)}> {currentGameState[1][1]} </button>
            <button onClick={() => setSquare(1,2)}> {currentGameState[1][2]} </button>
            <button onClick={() => setSquare(2,0)}> {currentGameState[2][0]} </button>
            <button onClick={() => setSquare(2,1)}> {currentGameState[2][1]} </button>
            <button onClick={() => setSquare(2,2)}> {currentGameState[2][2]} </button>
            <p> Winner: {winner}</p>
        </div>
            <button onClick={reset}>Reset</button>
        </div>
    )
};

